<template>
<button id="excelExport" style="margin-bottom: 25px;" @click="ExportToExcel('xlsx')" class="btn-hover color-8 is-pulled-left ">
    <span class="icon is-small">
      <i class="fas fa-file-excel"></i>
    </span>
    <span>Export to .xls</span>
</button>
<button @click="notesShow = !notesShow" style="margin-bottom: 25px;" class="btn-hover color-9 is-pulled-left"><b>{{notesToggle}}</b> Notes column</button>
<!-- <button @click="ownerShow = !ownerShow" class="btn-hover color-11 is-pulled-left">{{ownerToggle}} Owner column</button> -->
<input placeholder="Free text search Country or Network" id="input1" ref="searchInput" v-model="search" type="text" class="input is-large animate__animated animate__backInDown">
<!-- <h3 class="is-pulled-right"> Show ALL | 10 | 20 | 50 </h3> -->
    <div style="margin-top:10px;" class="table-container">
        <span class = "is-size-5">
               Show <a @click="showResults = 10"> 10</a>
               <a @click="showResults = 20"> 20</a>
               <a @click="showResults = 50"> 50</a>
               <a @click="showResults = 5000"> all</a>

            </span>
            <br>
<table id="mnotable" class="table is-bordered is-striped is-hoverable is-fullwidth">
  <thead class="has-background-black-bis">
      <!-- <tr class="has-background-white-bis">
      <th class="has-text-black-bis">Show ALL | 10 | 20 | 50</th>
    </tr> -->
    <tr>
      <th class="has-text-white-bis">Country</th>
      <th class="has-text-white-bis">Mcc</th>
      <th class="has-text-white-bis">MNC</th>
      <th class="has-text-white-bis">Operator</th>
      <th class="has-text-white-bis">Status</th>
      <th v-if="technologyShow" class="has-text-white-bis animate__animated animate__bounce">Technology</th>
      <th v-if="notesShow" class="has-text-white-bis animate__animated animate__bounce">Notes</th>
      <th v-if="ownerShow" class="has-text-white-bis animate__animated animate__bounce">Owner</th>

    </tr>
    
  </thead>
  <tbody>
    <tr v-for="row in searchResult.slice(0,showResults)">
      <th>{{row['Country']}}</th>
      <td>{{row['MCC']}}</td>
      <td>{{row['MNC']}}</td>
      <td>{{row['Operator']}}</td>
      <td @mouseover="hover = true" @mouseleave="hover = false">
           <span style="animate__animated animate__bounce" v-if="hover">{{row['Status']}}&ensp;</span><span class="icon is-small"><i v-bind:class="{ 'fas fa-check-circle': row['Status'] == 'Operational', 'fas fa-minus-circle': row['Status'].toLowerCase() == 'not operational', 'fas fa-exclamation-triangle': row['Status'] != 'not operational' && row['Status'] != 'Operational'}"></i></span></td>
      <!-- <td v-if="row['OperatorLink'].length == 0">{{row['Operator']}}</td>
      <td v-else @click="openModal(row['OperatorLink'], row['Operator'])"><a>{{row['Operator']}}</a></td> -->
      <td style="animate__animated animate__bounce" v-if="technologyShow"> {{row['Technology']}}</td>
      <td v-if="notesShow">{{row['References and notes']}}</td>
      <td v-if="ownerShow">{{row['Owner']}}</td>
    </tr>
  </tbody>
</table>
</div>
</template>
